@font-face {
  font-family: Roboto;
  src: url("Roboto-Regular.4f476b22.ttf");
}

@font-face {
  font-family: Roboto;
  src: url("Roboto-Medium.b7336e49.ttf");
  font-weight: 500;
}

:root {
  --color-active: #00acdc;
}

body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav__list {
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #000;
  align-items: center;
  height: 60px;
  font-family: Roboto, Helvetica, sans-serif;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav_logo {
  height: 40px;
}

.nav__link:hover, .is-active {
  color: var(--color-active);
}

.is-active:after {
  content: "";
  background-color: var(--color-active);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  right: 0;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}
